import React, { Component } from 'react';
import logo from "./logo.svg"
import SmoothCollapse from 'react-smooth-collapse';
import Match from './Match.js'
import StatSection from './StatSection.js'
import './App.css';
import NYG from './assets/NYG.svg';
import WAS from './assets/WAS.svg';
import NE from './assets/NE.svg';
import NYJ from './assets/NYJ.svg';
import MIN from './assets/MIN.svg';
import CHI from './assets/CHI.svg';
import DET from './assets/DET.svg';
import GB from './assets/GB.svg';
import IND from './assets/IND.svg';
import HOU from './assets/HOU.svg';
import PIT from './assets/PIT.svg';
import CLE from './assets/CLE.svg';
import PHI from './assets/PHI.svg';
import DAL from './assets/DAL.svg';
import LA from './assets/LA.svg';
import SF from './assets/SF.svg';
import LAC from './assets/LAC.svg';
import OAK from './assets/OAK.svg';
import DEN from './assets/DEN.svg';
import KC from './assets/KC.svg';
import TEN from './assets/TEN.svg';
import JAX from './assets/JAX.svg';
import TB from './assets/TB.svg';
import NO from './assets/NO.svg';
import MIA from './assets/MIA.svg';
import BUF from './assets/BUF.svg';
import SEA from './assets/SEA.svg';
import ARI from './assets/ARI.svg';
import ATL from './assets/ATL.svg';
import CAR from './assets/CAR.svg';
import BAL from './assets/BAL.svg';
import CIN from './assets/CIN.svg';



class App extends Component {

  state: Object = {
    expanded: false
  };

  _toggle() {
    this.setState({expanded: !this.state.expanded});
  }

  render() {
  const {expanded} = this.state;
    return (
      <div className="App">

        <header className="App-header">
          <img src={PHI} className="Top-logo" alt="logo" />
          <a href = "https://www.reddit.com/r/nflstreams/"><h1 className="App-title">Conal's NFL Page</h1></a>
        </header>
 

          <div className = "Matches-Holder">
						<Match team1={NYG} team2={WAS} date='6:00pm Sunday, December 31'/>
						<Match team1={NE} team2={NYJ} date='6:00pm Sunday, December 31'/>
						<Match team1={MIN} team2={CHI} date='6:00pm Sunday, December 31'/>
						<Match team1={DET} team2={GB} date='6:00pm Sunday, December 31'/>
						<Match team1={IND} team2={HOU} date='6:00pm Sunday, December 31'/>
						<Match team1={PIT} team2={CLE} date='6:00pm Sunday, December 31'/>
						<Match team1={PHI} team2={DAL} date='6:00pm Sunday, December 31'/>
						<Match team1={LA} team2={SF} date='9:25pm Sunday, December 31'/>
						<Match team1={LAC} team2={OAK} date='9:25pm Sunday, December 31'/>
						<Match team1={DEN} team2={KC} date='9:25pm Sunday, December 31'/>
						<Match team1={TEN} team2={JAX} date='9:25pm Sunday, December 31'/>
						<Match team1={TB} team2={NO} date='9:25pm Sunday, December 31'/>
						<Match team1={MIA} team2={BUF} date='9:25pm Sunday, December 31'/>
						<Match team1={SEA} team2={ARI} date='9:25pm Sunday, December 31'/>
						<Match team1={ATL} team2={CAR} date='9:25pm Sunday, December 31'/>
						<Match team1={BAL} team2={CIN} date='9:25pm Sunday, December 31'/>
          </div>  
	      <div className = "TagLine">
	      	<h3>All Times In GMT. Made with React {"<"}3 </h3>
	        <img src = {logo} className = "App-logo" alt = "logo"/> 
	      </div>
      </div>
    );
  }
}

export default App;


