import React, { Component } from 'react';
import './App.css';

class RightTeam extends Component { 
	render(){

		return(
			<div>
				<img src = {this.props.pic} className = "Team-Image" alt = "right team"/>
			</div>
		)
	}


}

export default RightTeam;


